body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background-color: #f8f8f8 !important;
}
.block {
	display: block;
	overflow: hidden;
}
code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}
.green {
	color: green;
}
.red {
	color: red;
}
.anticon-copy {
	color: blue !important;
}
.enable-copy-txt-box {
	cursor: copy;
	letter-spacing: 0px;
	text-align: justify;
	text-align-last: justify;
	font-family: "Courier New", Courier, monospace;
}
.enable-copy-icon-box {
	cursor: copy;
	letter-spacing: 0px;
	text-align: justify;
	text-align-last: justify;
	font-family: "Courier New", Courier, monospace;
}
.enable-copy-txt-box a:hover {
	border-bottom: 1px dotted #999;
}
.enable-copy-icon-box a:hover {
	border-bottom: 1px dotted #999;
}
.suffix {
	padding-left: 10px;
	color: #aaa;
}
.account-icon {
	border-radius: 100%;
	vertical-align: middle;
	border: 1px solid #ccc;
}
.bg-color {
	position: fixed;
	width: 500px;
	height: 500px;
	display: block;
	overflow: hidden;
	background-image: url("../public/img/bg-color.png");
	background-repeat: no-repeat;
	background-size: 100%;
	z-index: -1;
}
@media screen and (min-width: 901px) {
	.containner {
		margin-left: 198px;
		margin-top: 20px;
		margin-right: 26px;
		display: block;
		overflow: hidden;
		.bg-color-1 {
			right: 0px;
			top: -50px;
			background-position: 150px;
		}
		.bg-color-2 {
			bottom: 0;
			left: 0;
		}
	}
}
@media screen and (max-width: 900px) {
	.ant-card-body {
		margin: 0 !important;
		padding: 10px !important;
	}
	.containner {
		padding: 10px;
		display: block;
		overflow: hidden;
		.bg-color-1 {
			right: 0px;
			top: 0px;
			background-position: 150px;
		}
		.bg-color-2 {
			bottom: 0px;
			left: 0px;
			background-position: -215px 153px;
		}
	}
}

@primary-color: #1890ff;